<template>
  <div class="SnippetExplorer">
    <Search v-if="snippets"/>
    <Snippets :snippets="snippets" @select="selectSnippet" />
    <Button round icon><svg svg-inline="" @click="handleClick" role="presentation" focusable="false" xmlns="http://www.w3.org/2000/svg" width="18" height="19" viewBox="0 0 18 19"><g fill="none" fill-rule="evenodd" stroke="#FFF" stroke-linecap="square" stroke-width="3"><path d="M16.5 9.5h-15M9 2v15"/></g></svg></Button>
  </div>
</template>
<script>
import { mapActions } from 'vuex'
import Button from '../components/Button.vue';
import Search from '../components/snippetExplorer/Search.vue';
import Snippets from '../components/snippetExplorer/Snippets.vue';
import uniqid from 'uniqid';

export default {
  name: 'SnippetExplorer',
  components: {
    Button,
    Search,
    Snippets,
  },
  props: {
    snippets: Array,
  },
  data: () => ({}),
  methods: {
    handleClick() {
      let uID = uniqid();
      this.addSnippet(uID);
    },
    ...mapActions([
      'selectSnippet',
      'addSnippet'
    ])
  }
};

</script>
